import { defineComponent, ref, watch } from 'vue';
export default defineComponent({
  name: 'FForm',

  setup(props, {
    emit
  }) {
    const FormData = ref({ ...props.formData
    });
    watch(FormData, newValue => {
      emit('update:formData', newValue);
    }, {
      deep: true
    });
    return {
      FormData
    };
  },

  props: {
    FormItems: {
      type: Array,
      default: () => {
        return [];
      }
    },
    labelWidth: {
      type: String,
      default: '100px'
    },
    ItemStyle: {
      type: Object,
      default: () => {
        return {
          padding: '0 40px'
        };
      }
    },
    colLayout: {
      type: Object,
      default: () => {
        return {
          xl: 6,
          lg: 8,
          md: 12,
          sm: 24,
          xs: 24
        };
      }
    },
    formData: {
      type: Object,
      require: true
    }
  },
  emits: ['update:formData']
});