import { defineComponent, ref, watch } from 'vue';
import Fform from '@/base-ui/form/src/form.vue';
import { useStore } from '@/store';
export default defineComponent({
  setup(props) {
    const DialogVisible = ref(false);
    const formData = ref({});
    const store = useStore();
    watch(() => props.defaultInfo, newValue => {
      for (const item of props.ModalConfig?.FormItems) {
        formData.value[`${item.filed}`] = newValue[`${item.filed}`];
      }
    });

    const handleConfirmClick = () => {
      DialogVisible.value = false; //判断是编辑的弹框还是新建的弹框

      if (JSON.stringify(props.defaultInfo) == '{}') {
        store.dispatch('system/createPageDataAction', {
          pageName: props.pageName,
          pageInfo: { ...formData.value,
            ...props.otherInfo
          }
        });
      } else {
        store.dispatch('system/editPageDataAction', {
          pageName: props.pageName,
          pageInfo: { ...formData.value,
            ...props.otherInfo
          },
          id: props.defaultInfo.id
        });
      }
    };

    return {
      formData,
      DialogVisible,
      handleConfirmClick
    };
  },

  components: {
    Fform
  },
  props: {
    title: {
      type: String,
      require: true
    },
    ModalConfig: {
      type: Object,
      require: true
    },
    defaultInfo: {
      type: Object,
      default: () => ({})
    },
    otherInfo: {
      type: Object,
      default: () => ({})
    },
    pageName: {
      type: String,
      require: true
    }
  }
});